/* Form */
select { height: 4rem; font-size:1.4rem; }
input[type=tel],
input[type=time],
input[type=text],
input[type=password],
input[type=search],
input[type=email],
input[type=file],
input[type=url],
input[type=number],
input[type=date] { font-size:1.4rem; padding: 0 1.4rem; height: 4rem; }
input::-webkit-input-placeholder { font-size: 1.4rem; }
textarea { font-size:1.4rem; padding: 1.4rem; }
.label { font-size: 1.5rem; }

/* header */
header { padding: 0 1.3rem; }
header h1 { width: 13rem; padding-top: 0.5rem; }
header h1 img { width: 100%; }
.menu_dim { display: none; position: fixed; left: 0; top: 0; width: 100vw; height: 100vh; background-color: rgba(0,0,0,0.7); z-index: 99; }
.m_menuBtn { display: inline-block; margin-left: 4rem; width: 3.7rem; height: 2rem; background: url('../../assets/images/icon/mobile_menu.png') no-repeat center; }
.m_menuBtn,
header .language_box { height: 6rem; line-height: 6rem; }
header .language_btn { width: 2rem; height: 2rem; background-size: contain; }
.language_list { top: 5.3rem; }
.M_logo { height: 6rem; margin-bottom: 3rem; }
.M_logo img { width: 13rem; }
.M_logo .M_menuX  { position: absolute; top: 1.8rem; right: 1.4rem; width: 2rem; height: 2rem; background: url('../../assets/images/icon/M_x.png') no-repeat center; }
.menu_list { display: none; position: fixed; right: 0; top: 0; width: calc(100vw - 7.5rem); max-width: 30rem; min-height: 100vh; background-color: #fff; z-index: 100; padding: 1.4rem 1.8rem; }
.menu_list ul { padding-left: 1.7rem; }
.menu_list ul .depth_01 { display: block; height: 5rem; line-height: 5rem; font-size: 2rem; background: url('../../assets/images/icon/M_arrowBtm.png') no-repeat right 1.7rem center; }
.menu_list ul div { display: none; }
.menu_list ul div a { display: block; font-size: 1.5rem; color: #666; height: 3.5rem; line-height: 3.5rem; }
.menu_list ul div a::before { content: ""; display: inline-block; width: 0.3rem; height: 0.3rem; border-radius: 100%; background-color: #999; margin-right: 0.6rem; vertical-align: middle; }
.menu_list ul li.active .depth_01 { background-image: url('../../assets/images/icon/M_arrowTop.png'); color: #008dff; font-weight: 700; }
.menu_list.active { animation: m_menu 0.5s both; display: block; }
@keyframes m_menu
{
  from { right: -100%; }
  to { right: 0; }
}
header .menu_list li.active div { display: block; }

/* footer */
footer { padding: 2rem 1.2rem; }
footer .ft_logo { float: none; width: 13rem; }
footer .ft_txt { float: none; font-size: 1.3rem; margin-top: 1.5rem; }
footer .ft_txt li { margin-right: 1rem; }
footer .ft_txt li ~ li:before { display: none; }
footer .ft_link { position: absolute; top: 2rem; right: 1.2rem; }
footer .ft_link a { width: 3.5rem; height: 3.5rem; margin-left: 0.7rem; }

/* index */
#fullpage .main_middle { padding: 0 4rem; }
#fullpage section .main_middle.mid_top > h2,
#fullpage section .main_middle h2 { font-size: 4rem; letter-spacing: -0.15rem; }
