/* Reset */
html { font-size: 10px; }
body * { outline: none; box-sizing: border-box; }
html,body,h1,h2,h3,h4,h5,h6,div,p,blockquote,pre,code,address,ul,ol,li,menu,nav,section,article,aside,
dl,dt,dd,table,thead,tbody,tfoot,label,caption,th,td,form,fieldset,legend,hr,input,button,textarea,object,figure,figcaption {margin:0;padding:0;}
body{ font-size: 1rem; background:#fff;-webkit-text-size-adjust:none;word-wrap:break-word;word-break:keep-all;}
body,input,select,textarea,pre {border:none; font-size:1.7rem; font-family: 'AppleSDGothicNeo', sans-serif; color:#222; max-width: 2560px; margin: 0 auto; }
button { border:none; font-family: 'AppleSDGothicNeo', sans-serif; color:#fff; outline: none; cursor: pointer; background-color: transparent; }
ul,ol,li{list-style:none;}
table{ border-spacing:0; }
img,fieldset{border:0;}
address,cite,code,em{font-style:normal;font-weight:normal;}
label,img,input,select,textarea,button{vertical-align:middle;}
.hide,legend{overflow: hidden; display:block; position:absolute; border: 0; width: 1px; height: 1px; clip: rect(1px, 1px, 1px, 1px);}
hr{display:none;}
main,header,section,nav,footer,aside,article,figure{display:block;}
a{color:#222;text-decoration:none;}
pre{ white-space: -moz-pre-wrap; white-space: -pre-wrap; white-space: -o-pre-wrap; white-space: pre-wrap; word-wrap: break-word; color: #666; line-height: 3rem; }

/* rem rule */
@media (max-width: 320px)
{
    html { font-size: 7px; }
}
@media (min-width: 321px)
{
    html { font-size: 9px; }
}
@media (min-width: 375px)
{
    html { font-size: 10px; }
}
@media (min-width: 768px)
{
    html { font-size: 12px; }
}
@media (min-width: 1025px)
{
    html { font-size: 9px; }
}
@media (min-width: 1289px)
{
    html { font-size: 10px; }
}

/* Form */
select { height: 5rem; font-size:1.6rem; color:#222; border:1px solid #f3f3f3; border-radius: 0.5rem; padding-left: 1.6rem; background: url('../../assets/images/icon/icon_select.png') no-repeat right 1.5rem center; }
select{ -webkit-appearance:none; -moz-appearance:none; appearance:none; }
select::-ms-expand{ display:none; }
input[type=tel],
input[type=time],
input[type=text],
input[type=password],
input[type=search],
input[type=email],
input[type=file],
input[type=url],
input[type=number],
input[type=date] { font-size:1.6rem; color:#666; border:1px solid #f3f3f3; background:#fff; transition: all 0.5s; box-sizing: border-box; vertical-align:middle; border-radius: 0.5rem; padding: 0 1.6rem; height: 5rem; }
input::-webkit-input-placeholder{color:#bbbbbb; font-size:1.6rem; }
textarea { font-size:1.6rem; color:#666; border:1px solid #f3f3f3; background:#fff; transition: all 0.5s; box-sizing: border-box; vertical-align:middle; border-radius: 0.5rem; padding: 1.6rem; }
textarea::-webkit-input-placeholder { color: #bbb; }
