input[type=search][disabled],
input[type=email][disabled]{background:#fff; border-color:#c0c0c0; color:#666; -webkit-appearance:none;font-size:16px; padding: 0 12px; }
textarea[readonly],
textarea[disabled]{padding:11px; font-size:16px; color:#212121; font-weight:normal; line-height:140%; height:78px; background:#eaeaea;border:1px solid #c0c0c0;} */

.label { display: inline-block; padding-left: 2.8rem; background: no-repeat left center; color: #666; }

.check { background-image: url('../../assets/images/icon/un_check.png'); }
.ch_ra[type=checkbox]:checked + .check { background-image: url('../../assets/images/icon/check.png'); }

/* .radio { background-image: url('../../assets/images/icon/un_radio.png'); }
.ch_ra[type=radio]:checked + .radio { background-image: url('../../assets/images/icon/radio.png'); } */

.clear {clear:both;}
.clear:after { content:""; display:block; clear:both;}
.m0auto { width: 129rem; margin: 0 auto; }

/* 공통 */
/* float */
.left { float: left; }
.right { float: right; }

/* display */
.dis_block { display: block; }
.dis_inblock { display: inline-block; }
.dis_none { display: none; }

/* margin */
.marleft_10 { margin-left: 1rem; }
.marleft_140 { margin-left: 14rem; }
.martop_16 { margin-top: 1.6rem; }
.martop_20 { margin-top: 2rem; }
.martop_30 { margin-top: 3rem; }
.martop_40 { margin-top: 4rem; }
.martop_70 { margin-top: 7rem; }
.martop_94 { margin-top: 9.4rem; }
.martop_108 { margin-top: 10.8rem; }
.marbtm_12 { margin-bottom: 1.2rem; }
.marbtm_20 { margin-bottom: 2rem; }
.marbtm_26 { margin-bottom: 2.6rem; }
.marbtm_40 { margin-bottom: 4rem; }
.marbtm_100 { margin-bottom: 10rem; }

/* width */
.w_100 { width: 10rem; }
.w_130 { width: 13rem; }
.w_150 { width: 15rem; }
.w_160 { width: 16rem; }
.w_200 { width: 20rem; }
.w_220 { width: 22rem; }
.w_240 { width: 24rem; }
.w_280 { width: 28rem; }
.w_minus200 { width: calc(100% - 20rem); }
.w_minus450 { width: calc(100% - 45rem); }
.w_minus620 { width: calc(100% - 62rem); }
.w_minus820 { width: calc(100% - 82rem); }
.w_minus850 { width: calc(100% - 85rem); }
.w_half { width: 50%; }
.w_halfminus_30 { width: calc(50% - 3rem) }
.w_half.m_wfull ~ .m_wfull { padding-left: 1.5rem; }
.w_full { width: 100%; }

/* button */
.btn_center { text-align: center; }
.skyblue_btn { display: inline-block; height: 5rem; line-height: 5rem; border-radius: 0.5rem; background-color: #008dff; text-align: center; color: #fff; font-size: 1.6rem; }


/* text-align */
.txt_left { text-align: left; }
.txt_right { text-align: right; }
.txt_center { text-align: center; }

/* header */
header { position: fixed; left: 0; right: 0; top: 0; background-color: #fff; padding: 0 6rem; z-index: 100; border-bottom: 1px solid #ebebeb; max-width: 2560px; margin: 0 auto; }
header h1 { cursor: pointer; }
header .language_box { display: inline-block; position: relative; height: 10rem; line-height: 10rem; }
header .language_btn { position: relative; width: 2.6rem; height: 2.6rem; background: url('../../assets/images/icon/icon_language.png') no-repeat center / contain; }
header .language_btn:before { content: ""; display: block; width: 1rem; height: 0.7rem; background: url('../../assets/images/icon/icon_language_arrow.png') no-repeat center / contain; position: absolute; right: -2.2rem; top: calc(50% - 0.35rem); transition: all 0.3s; }
.language_list { display: none; position: absolute; top: 7.3rem; border: 1px solid #ebebeb; background-color: #fff; }
.language_list button { display: block; width: 6rem; height: 2.4rem; color: #222; font-size: 1.3rem; font-weight: 700; }
.language_list button ~ button { border-top: 1px solid #ebebeb; }

@media (min-width: 1289px)
{
    header .menu_list { width: calc(100% - 23.2rem); }
}
@media (min-width: 1025px)
{
    header h1 { padding-top: 2.8rem; }
    header .menu_list { display: inline-block; width: calc(100% - 25.5rem); padding-right: 9rem; text-align: right; font-size: 0; }
    header .menu_list li { position: relative; display: inline-block; font-size: 2rem; padding: 0 3.5rem; }
    header .menu_list li .depth_01 { position: relative; display: block; height: 10rem; line-height: 10rem; cursor: pointer; }
    header .menu_list li .depth_01:after { content: ""; display: none; position: absolute; bottom: 0; left: 0; width: 100%; height: 0.6rem; background-color: #008dff; animation-duration: .4s; animation-name: menu_bar; }
    header .menu_list li div { display: none; position: absolute; top: 10rem; left: auto; text-align: left; border: 1px solid #ebebeb; background-color: #fff; animation-duration: .4s; animation-name: menu_hover; }
    header .menu_list li div a { display: block; font-size: 1.7rem; width: 16.8rem; height: 4.9rem; line-height: 4.9rem; padding-left: 2rem; transition: all 0.2s; }
    header .menu_list li div a:hover { background-color: #f6f6f6; }
    header .menu_list li div a ~ a { border-top: 1px solid #ebebeb; }
    .M_logo,
    .m_menuBtn { display: none; }
    header .language_box:hover .language_list,
    header .menu_list li:hover div,
    header .menu_list li:hover .depth_01:after { display: block; }
    header .language_box:hover .language_btn:before { transform: rotate(180deg); }

}

header .language_box.active .language_list,
header .menu_list li.active .depth_01:after { display: block; }
header .language_box.active .language_btn:before { transform: rotate(180deg); }


@media (min-width: 1025px) and (max-width: 1330px)
{
    header .menu_list { padding-right: 2%; }
    header .menu_list li { padding: 0 3%; }
}
@keyframes menu_bar
{
    from { width: 0; }
    to { width: 100%; }
}
@keyframes menu_hover
{
    from { opacity: 0; }
    to { opacity: 1; }
}

/* footer */
footer { position: relative; background-color: #fff; padding: 4.5rem 6rem; border-top: 1px solid #ebebeb; }
footer .ft_logo { margin-right: 5.4rem; }
footer .ft_logo img { width: 100%; }
footer .ft_txt { font-size: 1.5rem; color: #999; line-height: 2.1rem; }
footer .ft_txt li { display: inline-block; }
footer .ft_txt li ~ li:before { content: ""; display: inline-block; width: 1px; height: 1.1rem; background-color: #e9e7e7; margin: 0 1rem; vertical-align: middle; }
footer .ft_link a { display: inline-block; margin-left: 1rem; }
footer .ft_link a img { width: 100%; }

/* index */
main { padding-top: 10rem; min-height: calc(100vh - 15.5rem); }
#fullpage footer { z-index: 1; height: 16rem !important; }
#fullpage footer .fp-tableCell { height: 6rem !important; }
#fullpage section { position: relative; text-align: center; }
#fullpage section:not(.main_03) { color: #fff; }
#fullpage section .main_middle > h2 { font-size: 7.2rem; }
#fullpage section .main_middle > p { font-size: 2.8rem; }
.main_middle { position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); width: 100%; }
.main_middle.mid_top { top: 19.8rem; left: 6rem; z-index: 2; transform: translate(0,0); }
.main_middle.mid_top * { text-shadow: 0px 3px 20px rgba(0,0,0,0.5); }
#fullpage section .main_middle.mid_top > h2 { font-size: 5.5rem; color: #E0F488; }
#fullpage section .main_middle.mid_top > p { font-size: 2.2rem; color: #E0F488; }
.main_01 video { position: fixed; right: 50%; top: 10rem; min-width: 100%; min-height: 100%; transform: translateX(50%); }
.main_01 video source { height: 100vh; }
.main_01 { background: url('../../assets/images/img_main_01_Moment.jpg') no-repeat center / cover; }
.main_02 { background: url('../../assets/images/img_main_02.png') no-repeat center / cover }
.main_04 { background: url('../../assets/images/img_main_03.png') no-repeat center / cover }
.main_02 h2 { padding-top: 11rem; background: url('../../assets/images/icon/main_ico01.png') no-repeat top 0 center; }
.main_03 h2 { padding-top: 11rem; background: url('../../assets/images/icon/main_ico02.png') no-repeat top 0 center; }
.main_04 h2 { padding-top: 11rem; background: url('../../assets/images/icon/main_ico03.png') no-repeat top 0 center; }
.main_arrow { position: absolute; bottom: 4rem; left: calc(50% - 2.5rem); display: block; width: 5rem; height: 5rem; background: url('../../assets/images/icon/main_btm.png') no-repeat; animation: main_btmAr 0.8s 1s alternate infinite }
.main_link { display: block; padding-left: 3rem; width: 24rem; height: 6rem; line-height: 6.3rem; margin-left: auto; margin-right: auto; background: #fff url('../../assets/images/icon/blue_arrow.png') no-repeat right 2.5rem center; border-radius: 3rem; font-size: 1.9rem; text-align: left; font-weight: 700; }
.vimeo-wrapper { position: fixed; top: 0; left: 0; width: 100%; height: 100%; pointer-events: none; overflow: hidden; margin-top: 10rem; }
.vimeo-wrapper iframe { width: 100vw; height: 56.25vw; min-height: 100vh; min-width: 177.77vh; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); }

@keyframes main_btmAr
{
    0% { transform: translateY(-2rem); }
    100% { transform: translateY(0rem) }
}
#news.swiper-container { max-width: 129rem; padding-left: 0.7rem; text-align: left; margin-top: 4.2rem; }
.list_txt { padding: 2.2rem 2.5rem; border-top: 1px solid #ebebeb; }
.main_03 .list_txt { border: 1px solid #ebebeb; }
.list_txt em { display: block; padding-bottom: 1rem; color: #bbbbbb; font-size: 1.7rem; }
.list_txt p { margin-bottom: 2.2rem; font-weight: 700; font-size: 1.9rem; height: 8.3rem; overflow: hidden; display: -webkit-box; -webkit-line-clamp: 3; -webkit-box-orient: vertical; line-height: 2.8rem; }
.list_txt a { display: block; font-size: 1.6rem; background: url('../../assets/images/icon/blue_arrow.png') no-repeat right 0 center / 2rem; }
.news_btn { position: absolute; top: 16rem; right: 50%; transform: translateX(50%); width: 129rem; height: 3.6rem; }
.main_03 .news-next,
