.main_03 .news-prev { right: 8.2rem; left: auto; background-image: url('../../assets/images/icon/news_left.png'); }
.main_03 .news-next { right: 4.6rem; background-image: url('../../assets/images/icon/news_right.png')}
.news_plus { position: absolute; width: 3.6rem; height: 3.6rem; right: 0; background: url('../../assets/images/icon/news_plus.png') no-repeat center }

.page_bar { position: fixed; right: 3.5rem; top: 50%; z-index: 90; }
.page_bar::before { content: ""; display: block; width: 1px; height: 29.7rem;  position: absolute; right: 2.5rem; top: 0.8rem; z-index: 100; background-color: rgba(255, 255, 255, 0.15); }
.page_bar a { position: relative; display: block; width: 100%; text-align: right; font-size: 1.5rem; margin-bottom: 7.4rem; color: #fff; }
.page_bar a:before { content: ""; display: none; width: 5rem; height: 5rem; border-radius: 100%; background-color: rgba(255,255,255,0.2); position: absolute; right: 0; top: -1.5rem; }
.page_bar a:after { content: ""; display: inline-block; width: 1.6rem; height: 1.6rem; background-color: #fff; border-radius: 100%; margin-right: 1.7rem; margin-left: 2.7rem; vertical-align: middle; }
.page_bar a span { display: none; }
.page_bar a.active span { display: inline-block; }
.page_bar a.active::before { display: block; }
body.fp-viewing-slide_03 .page_bar a { color: #9a9a9a; }
body.fp-viewing-slide_03 .page_bar::before { background-color: rgba(154,154,154,0.15); }
body.fp-viewing-slide_03 .page_bar a:before { background-color: rgba(154,154,154,0.2); }
body.fp-viewing-slide_03 .page_bar a:after { background-color: #9a9a9a; }

