.highflier_list li { position: relative; display: inline-block; width: calc(25% - 2.25rem); padding-top: calc(25% - 3.2rem); border-radius: 100%; border: 0.5rem solid #fff; }
.highflier_list li ~ li { margin-left: 3rem; }
.highflier_list li div { position: absolute; top: 0; right: 0; bottom: 0; left: 0; padding-top: 4.6rem; }
.highflier_list strong { display: block; padding: 2rem 0 1rem; font-size: 3.4rem; }
